import { ApiService } from './../../../core/services/api.service';
import { Component } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { BaseComponent } from '../../../core/components/base.component';
import { Router } from '@angular/router';
import { UserType } from '../../../core/model/user';

@Component({
  selector: 'mvp-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent extends BaseComponent {
  userTypes = UserType;
  formName = 'authForm';
  serviceError = false;
  authForm = this._fb.group({
    username: ['', Validators.required],
    password: ['', Validators.required],
    userType: [UserType.PATIENTS, Validators.required]
  })

  constructor(private _api: ApiService, private _fb: FormBuilder,private _routes: Router) {
    super();
  }

  save() {
    this._api.initLogin(this.authForm.value).subscribe(() => {
      this.serviceError = false;
      this._routes.navigate(['dashboard']);
    }, () => {
      this.serviceError = true;
    });
  }
}
