import { Component, Input, Output, EventEmitter } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'mvp-incoming',
  template: `
    <div class="modal-header">
      <h4 class="modal-title" id="modal-title">
        <h4>
          Call from <strong class="j-ic_initiator">{{ name }}</strong>
        </h4>
      </h4>
    </div>
    <div class="modal-footer">
      <button
        type="button"
        class="btn btn-danger j-decline"
        (click)="dismiss()"
      >
        Decline
      </button>
      <button
        type="button"
        ngbAutofocus
        class="btn btn-primary j-accept"
        (click)="success()"
      >
        Accept
      </button>
    </div>
  `
})
export class IncomingComponent {
  @Input() name;
  @Output() accept = new EventEmitter();
  @Output() decline = new EventEmitter();
  constructor(public activeModal: NgbActiveModal) {}
  success(val = null) {
    const data = {
      type: 'accept',
      data: val
    };
    this.accept.emit(data);
    this.activeModal.close(data);
  }

  dismiss(val = null) {
    const data = {
      type: 'decline',
      data: val
    };
    this.decline.emit(data);
    this.activeModal.close(data);
  }
}
