import { SessionService, SESSION_KEY } from './../../core/services/session.service';
import { Injectable, OnDestroy } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { Subscription } from 'rxjs';

@Injectable()
export class LoggedInGuard implements CanActivate, OnDestroy {
  isLoggedIn:boolean;
  private _sessionSub$: Subscription;

  constructor(private _session:SessionService, private _routes: Router) {
    this._sessionSub$ = this._session.select(SESSION_KEY.USER).subscribe((data) => {
      this.isLoggedIn = data.isLoggedIn;
    })
  }

  canActivate():boolean {
    if(!this.isLoggedIn) {
      this._routes.navigate(['login']);
    }
    return this.isLoggedIn;
  }

  ngOnDestroy() {
    this._sessionSub$.unsubscribe();
  }
}
