
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { CoreModule } from './modules/core/core.module';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { ChatComponent } from './components/chat/chat.component';

import { AuthModule } from './modules/auth/auth.module';
import { AppRoutingModule } from './app.routing';
import { VideoCallComponent } from './components/video-call/video-call.component';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { IncomingComponent } from './components/video-call/incoming-call.component';

@NgModule({
  declarations: [AppComponent, DashboardComponent, ChatComponent, VideoCallComponent, IncomingComponent],
  imports: [
    BrowserModule,
    CoreModule,
    AppRoutingModule,
    AuthModule,
    NgbModule
  ],
  entryComponents: [IncomingComponent],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule {}
