import { Injectable } from '@angular/core';
import * as _ from 'lodash';
import { Subject, BehaviorSubject } from 'rxjs';
import { SessionService, SESSION_KEY } from '../../modules/core/services/session.service';
import { Router } from '@angular/router';
declare var QB;

@Injectable({
  providedIn: 'root'
})
export class VideoCallService {
  CONFIG = {
    CREDENTIALS: {
      appId: 77722,
      authKey: 'cg8XYe6SOzWQ-24',
      authSecret: '8bBndFHddHAwEUh'
    },
    APP_CONFIG: {
      debug: true,
      webrtc: {
        answerTimeInterval: 30,
        dialingTimeInterval: 5,
        disconnectTimeInterval: 35,
        statsReportTimeInterval: 5
      }
    }
  };
  deviceId = {
    video: undefined,
    audio: undefined
  };
  session;
  caller;
  callee;
  calleeId = new BehaviorSubject('');
  onRemoteStreamListener = new Subject();
  incomingCall = new Subject();
  constructor(private _session: SessionService, private _router: Router) {
    this._session.select(SESSION_KEY.USER).subscribe(data => {
      if(data.isLoggedIn) {
        this._initQBSession();
        this.fillMediaSelects();
        this.login(data.user.videoID);
      }
    });
  }

  private _initQBSession() {
    QB.init(
      this.CONFIG.CREDENTIALS.appId,
      this.CONFIG.CREDENTIALS.authKey,
      this.CONFIG.CREDENTIALS.authSecret,
      this.CONFIG.APP_CONFIG
    );
    QB.webrtc.onRemoteStreamListener = (session, userId, stream) => {
      const state = this.session.connectionStateForUser(userId),
        peerConnList = QB.webrtc.PeerConnectionState;
      if (
        state === peerConnList.DISCONNECTED ||
        state === peerConnList.FAILED ||
        state === peerConnList.CLOSED
      ) {
        return false;
      }
      this.session.peerConnections[userId].stream = stream;
      this.session.attachMediaStream('remote_video_' + userId, stream);
    };
    QB.webrtc.onCallListener = (session, extension) => {
      this.session = session;
      if (this.session.state !== QB.webrtc.SessionConnectionState.CLOSED) {
        this.incomingCall.next(session.initiatorID);
      }
    };
  }

  fillMediaSelects() {
    return new Promise((resolve, reject) => {
      navigator.mediaDevices
        .getUserMedia({
          audio: true,
          video: true
        })
        .then(stream => {
          this.showMediaDevices('videoinput')
            .then(() => {
              return this.showMediaDevices('audioinput');
            })
            .then(() => {
              stream.getTracks().forEach(track => {
                track.stop();
              });
              resolve();
            });
        })
        .catch(error => {
          this.showMediaDevices('videoinput')
            .then(() => {
              return this.showMediaDevices('audioinput');
            })
            .then(() => {
              resolve();
            });
        });
    });
  }
  showMediaDevices(kind) {
    return new Promise((resolve, reject) => {
      QB.webrtc
        .getMediaDevices(kind)
        .then(devices => {
          const isVideoInput = kind === 'videoinput';
          if (isVideoInput) {
            this.deviceId.video = devices[0].deviceId;
          } else {
            this.deviceId.audio = devices[0].deviceId;
          }
          resolve();
        })
        .catch(function(error) {
          reject();
        });
    });
  }

  login(login) {
    this._login(login).then(
      (data: any) => {
        this.caller = data;
        QB.chat.connect(
          {
            jid: QB.chat.helpers.getUserJid(
              data.id,
              this.CONFIG.CREDENTIALS.appId
            ),
            password: 'quickblox'
          },
          (err, res) => {
            if (err) {
              console.log(err);
              alert('chat err');
            } else {
              console.log(res);
              alert('chat successs');
            }
          }
        );
      },
      function(error) {
        try {
          if (typeof error.message === 'string') {
            alert(error.message);
          } else {
            alert(error.message.errors.base[0]);
          }
        } catch (e) {
          console.log(error);
        }
      }
    );
  }

  _login(login) {
    const userRequiredParams = {
      login: login,
      password: 'quickblox'
    };
    return new Promise((resolve, reject) => {
      QB.createSession((csErr, csRes) => {
        if (csErr) {
          reject(csErr);
        } else {
          /** In first trying to login */
          QB.login(userRequiredParams, (loginErr, loginUser) => {
            if (loginErr) {
              console.log(loginErr);
            } else {
              resolve(loginUser);
            }
          });
        }
      });
    });
  }

  call(callerID, attendee, videoElem) {
    this._router.navigate(['video-call']);
    const calleesIds = [attendee]; // User's ids
    const sessionType = QB.webrtc.CallType.VIDEO; // AUDIO is also possible
    const additionalOptions = { bandwith: 512 }; // The video bandwith (number, optional, 0 = unlimited)
    const mediaParams = {
      audio: {
        deviceId: this.deviceId.audio
      },
      video: {
        deviceId: this.deviceId.video
      },
      options: {
        muted: true,
        mirror: true
      },
      elemId: videoElem //'main_video'
    };
    /** Check internet connection */
    if (!window.navigator.onLine) {
      alert('not online');
      return false;
    }
    this.session = QB.webrtc.createNewSession(
      calleesIds,
      sessionType,
      callerID,
      additionalOptions
    );
    this.session.getUserMedia(mediaParams, (err, stream) => {
      if (
        err ||
        !stream.getAudioTracks().length ||
        !stream.getVideoTracks().length
      ) {
        alert('getmedia err');
        this.session.stop({});
      } else {
        const callParameters = {};
        const pushRecipients = [];
        this.session.call({}, () => {
          if (!window.navigator.onLine) {
            this.session.stop({});
            alert('callerrror');
          } else {
            alert('calling');
            this.callee = {
              userID: attendee,
              name: attendee,
              state: 'connecting'
            };
            this.calleeId.next(attendee);
            pushRecipients.push(parseInt(attendee, 1));
          }
        });
        // and also send push notification about incoming call
        // (corrently only iOS/Android users will receive it)
        //
        const params = {
          notification_type: 'push',
          user: { ids: pushRecipients },
          environment: 'development',
          message: QB.pushnotifications.base64Encode(
            this.caller.full_name + ' is calling you'
          )
        };
        //
        QB.pushnotifications.events.create(params, (err1, response) => {
          if (err1) {
            console.log(err1);
            alert('pushnotifications err' + JSON.stringify(err1));
          } else {
            // success
            console.log('Push Notification is sent.');
            alert('Push Notification is sent.');
          }
        });
      }
    });
  }

  accept(videoElem) {
    this._router.navigate(['video-call']);
    const mediaParams = {
      audio: {
        deviceId: this.deviceId.audio
      },
      video: {
        deviceId: this.deviceId.video
      },
      elemId: videoElem,
      options: {
        muted: true,
        mirror: true
      }
    };
    this.session.getUserMedia(mediaParams, (err, stream) => {
      if (
        err ||
        !stream.getAudioTracks().length ||
        !stream.getVideoTracks().length
      ) {
        alert('getmedia err');
        this.session.stop({});
      } else {
        this.callee = {
          userID: this.session.initiatorID,
          name: this.session.initiatorID,
          state: 'incoming call'
        };
        this.calleeId.next(this.session.initiatorID);
        this.session.accept({});
      }
    });
  }

  decline() {
    if (!_.isEmpty(this.session)) {
      this.session.reject({});
    }
  }
}
