import { Router } from '@angular/router';
import { SessionService, SESSION_KEY } from './../../services/session.service';
import { Component, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { User } from '../../model/user';
import { COUNSELLOR } from 'apps/frontend/src/app/modules/core/model/doctor-list';

@Component({
  selector: 'mvp-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnDestroy{
  user :User;
  private _sessionSub$: Subscription;
  councellor = COUNSELLOR;
  constructor(private _session: SessionService,private _routes: Router) {
    this._sessionSub$ = this._session.select(SESSION_KEY.USER).subscribe(data => {
      this.user = data.user;
    })
  }

  logout() {
    this._session.logOut();
    this._routes.navigate(['login']);
  }

  ngOnDestroy() {
    this._sessionSub$.unsubscribe();
  }
}
