import { Injectable } from '@angular/core';
import { AbstractControl, FormGroup } from '@angular/forms';
import * as _ from 'lodash';

@Injectable()
export class ValidationService {

  private readonly _errorMessages = {
    required: (name) => `${name} is required`,
    minlength: (name, data) => `${name} must have minimum ${data.requiredLength} characters`,
    maxlength: (name, data) => `${name} must have maximum ${data.requiredLength} characters`
  };

  getErrorMsg(formControl: AbstractControl) {
    if (formControl.errors) {
      const firstRule = _.head(Object.keys(formControl.errors));
      const displayControlName = this.getControlName(formControl);
      return (this._errorMessages[firstRule] &&
        this._errorMessages[firstRule](displayControlName, formControl.errors[firstRule])) || 'Invalid ' + displayControlName;
    }
  }

  getControlName(formControl: AbstractControl) {
    return _.startCase(this._getControlName(formControl));
  }

  private _getControlName(control: AbstractControl): string | null {
    const group = <FormGroup>control.parent;
    if (!group) {
      return null;
    }
    let name: string;
    Object.keys(group.controls).forEach(key => {
      const childControl = group.get(key);
      if (childControl !== control) {
        return;
      }
      name = key;
    });
    return name;
  }
}
