import { Subscription } from 'rxjs';
import { SessionService, SESSION_KEY } from './modules/core/services/session.service';
import { Component, OnDestroy } from '@angular/core';
import { VideoCallService } from './components/video-call/video-call.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { IncomingComponent } from './components/video-call/incoming-call.component';

@Component({
  selector: 'mvp-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnDestroy {
  title = 'frontend';
  isLoggedIn;
  private _sessionSub$: Subscription;

  constructor(private _session: SessionService, private _videoCall: VideoCallService, private _modalService: NgbModal) {
    this._sessionSub$ = this._session.select(SESSION_KEY.USER).subscribe((data) => {
      this.isLoggedIn = data.isLoggedIn;
    });
    this._videoCall.incomingCall.subscribe(initiatorID => {
      this._incomingCall(initiatorID);
    });
  }

  private _incomingCall(initiatorID) {
    const modalRef = this._modalService.open(IncomingComponent, {
      backdrop: 'static'
    });
    modalRef.componentInstance.name = initiatorID;
    modalRef.componentInstance.accept.subscribe(() => {
      this._videoCall.accept('main_video');
    });
    modalRef.componentInstance.decline.subscribe((data) => {
      this._videoCall.decline();
    });
  }

  ngOnDestroy() {
    this._sessionSub$.unsubscribe();
  }
}
