import { HTTP_METHODS } from './../model/service';
import { serviceInfo } from './../config/service-info';
import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import * as _ from 'lodash';
import { map, catchError } from 'rxjs/operators';
import { environment } from '../../../../environments/environment';
import { IServiceInfo } from '../model/service';


@Injectable()
export class ServiceInterceptor implements HttpInterceptor {

  private _init(serviceName: string): IServiceInfo {
    if (_.has(serviceInfo, serviceName)) {
      return {
        ..._.cloneDeep(_.get(serviceInfo, serviceName)),
        serviceName: serviceName
      };
    }
  }

  private _getMethod(config: IServiceInfo) {
    return environment.loadFromAsset ? HTTP_METHODS.GET : config.method;
  }

  private _getUrl(reqUrl: string, config: IServiceInfo) {
    const endpoint = environment.loadFromAsset ? config.operationName + '.json' : config.operationName+'/'+config.version;
    return `${environment.baseUrl}${endpoint}`;
  }

  private _getHeaders(config: IServiceInfo) {
    const defaultHeaders = {
      'Accept': 'application/json',
      'Content-Type': 'application/json',
    };
    // if (config.isAuthenticated && sessionStorage.getItem('authToken')) {
    //   defaultHeaders['authToken'] = sessionStorage.getItem('authToken');
    // }
    return defaultHeaders;
  }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<any> {
    const config = this._init(req.url);
    if (!_.isEmpty(config)) {
      const apiReq = req.clone({
        method: this._getMethod(config),
        url: this._getUrl(req.url as any, config),
        setHeaders: this._getHeaders(config)
      });
      return next.handle(apiReq)
        .pipe(
          map(res => this.handleResponse(res, config)),
          catchError((e) => {
            throw e;
          })
        );
    }
    return next.handle(req);
  }

  private _isSuccess(data) {
    return _.has(data, 'statusCode') && data.statusCode === '0000';
  }

  private _getNewResponse(res, result) {
    return new HttpResponse({
      body: result,
      headers: res.headers,
      status: res.status,
      statusText: res.statusText,
      url: res.url
    });
  }


  protected handleResponse(res, service: IServiceInfo) {
    if (res instanceof HttpResponse) {
      const result: any = res.body;
      if (this._isSuccess(result)) {
        return this._getNewResponse(res, result.details || result);
      } else {
        this._returnError(result);
      }
    }
    return res;
  }

  private _returnError(data) {
    throw new HttpErrorResponse({
      error: _.get(data, 'details.message', 'Error'),
      status: _.get(data, 'header.statusCode'),
      statusText: _.get(data, 'header.statusDescription')
    });
  }
}
