import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { LoggedInGuard } from './modules/auth/guard/logged-in.guard';
import { LoginComponent } from './modules/auth/components/login/login.component';
import { NotLoggedInGuard } from './modules/auth/guard/not-logged-in.guard';
import { ChatComponent } from './components/chat/chat.component';
import { VideoCallComponent } from './components/video-call/video-call.component';

const routes: Routes = [
  {
    path: 'dashboard',
    component: DashboardComponent,
    canActivate: [LoggedInGuard]
  },
  {
    path: 'chat',
    component: ChatComponent,
    canActivate: [LoggedInGuard]
  },
  {
    path: 'login',
    component: LoginComponent,
    canActivate: [NotLoggedInGuard]
  },  
  {
    path: 'video-call',
    component: VideoCallComponent,
    canActivate: [LoggedInGuard]
  },
  {
    path: 'appointments',
    loadChildren: './modules/featured/appointments/appointments.module#AppointmentsModule',
    canActivate: [LoggedInGuard]
  },
  // {
  //   path: '**',
  //   redirectTo: '/login'
  // },
  {
    path: '',
    redirectTo: '/login',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      initialNavigation: 'enabled',
      useHash: true
    })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
