import { IDoctorListState, InitialDoctorListState } from './../model/doctor-list';
import { BehaviorSubject } from 'rxjs';
import { Injectable } from '@angular/core';
import * as _ from 'lodash';
import { IUserState, InitialUserState } from '../../core/model/user';

export enum SESSION_KEY {
  USER = 'user',
  DOCTOR_LIST = 'doctorList'
};

export type SessionState = {
  [key in SESSION_KEY]: BehaviorSubject<any>;
};

@Injectable({
  providedIn: 'root'
})
export class SessionService {
  private _sessionSupport = true;
  private _state: SessionState = {
    [SESSION_KEY.USER]: new BehaviorSubject<IUserState>(_.cloneDeep(InitialUserState)),
    [SESSION_KEY.DOCTOR_LIST]: new BehaviorSubject<IDoctorListState>(_.cloneDeep(InitialDoctorListState))
  };

  constructor() {
    try {
      if (sessionStorage) {
        _.each(Object.keys(SESSION_KEY), (eachKey) => {
          if (this._hasItem(<SESSION_KEY>SESSION_KEY[eachKey])) {
            this.setState(<SESSION_KEY>SESSION_KEY[eachKey], this._getItem(<SESSION_KEY>SESSION_KEY[eachKey]));
          }
        });
      }
    } catch (e) {
      this._sessionSupport = false;
      console.warn('Unable to access session storage');
    }
  }

  select(key: SESSION_KEY) {
    if (this._state[key]) {
      return this._state[key].asObservable();
    } else {
      throw Error(`session ${key} is not defined`);
    }
  }


  setState(key: SESSION_KEY, data, mergeArray = false) {
    const nextValue = _.mergeWith({}, _.cloneDeep(this._state[key].value), data, (objValue, srcValue) => {
      if (_.isArray(objValue)) {
        return mergeArray ? [
          ...srcValue,
          ...objValue
        ] : srcValue;
      }
    });
    if (this._sessionSupport) {
      sessionStorage.setItem(key, JSON.stringify(nextValue));
    }
    this._state[key].next(nextValue);
  }

  logOut() {
    this._state[SESSION_KEY.USER].next(_.cloneDeep(InitialUserState));
    if (this._sessionSupport) {
      sessionStorage.clear();
    }
  }

  private _hasItem(key: SESSION_KEY) {
    return (this._sessionSupport && _.has(sessionStorage, key));
  }

  private _getItem(key: SESSION_KEY) {
    if (this._hasItem(key)) {
      const val = JSON.parse(sessionStorage.getItem(key));
      return val;
    }
  }
}
