import { Component, OnDestroy } from '@angular/core';
import { SessionService, SESSION_KEY } from '../../modules/core/services/session.service';
import { Subscription } from 'rxjs';
import { User } from '../../modules/core/model/user';

@Component({
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnDestroy {
  user :User;
  private _sessionSub$: Subscription;

  constructor(private _session: SessionService) {
    this._sessionSub$ = this._session.select(SESSION_KEY.USER).subscribe(data => {
      this.user = data.user;
    })
  }

  ngOnDestroy() {
    this._sessionSub$.unsubscribe();
  }
}
