import { Component, OnInit, ElementRef, Input } from '@angular/core';
import { AbstractControl } from '@angular/forms';
import { distinctUntilChanged } from 'rxjs/operators';
import * as _ from 'lodash';
import { BehaviorSubject } from 'rxjs';
import { ValidationService } from '../validation.service';

@Component({
  selector: 'mvp-err-text',
  templateUrl: './err-text.component.html',
  styleUrls: ['./err-text.component.css']
})
export class ErrTextComponent implements OnInit {
  isVisible = true;
  private _submitted: boolean;
  private _curentElem: any;
  private _errors: any;
  message: string;

  @Input() private control: AbstractControl;
  @Input() submitted: BehaviorSubject<boolean>;

  constructor(elRef: ElementRef, private _validation: ValidationService) {
    this._curentElem = elRef.nativeElement.parentElement.querySelector('input, select, textarea');
  }

  ngOnInit() {
    this.control.statusChanges.pipe(distinctUntilChanged()).subscribe(state => {
      this.setVisibilty();
    });
    this.control.valueChanges.subscribe(val => {
      if (!_.isEqual(this._errors, this.control.errors)) {
        this.setErrorMsg();
      }
    });
    this.submitted.subscribe(value => {
      this._submitted = value;
      this.setVisibilty();
    });
  }

  setVisibilty() {
    const hasError = this.control && this.control.errors && this._submitted;
    if (this._curentElem && this.control) {
      if (hasError) {
        this._curentElem.classList.add('is-invalid');
      } else {
        this._curentElem.classList.remove('is-invalid');
      }
    }
    this.isVisible = hasError;
    this.setErrorMsg();
  }

  setErrorMsg() {
    this._errors = this.control.errors;
    this.message = this._validation.getErrorMsg(this.control);
  }

}
