import { SessionService, SESSION_KEY } from './../../modules/core/services/session.service';
import { Component, OnInit } from '@angular/core';
import { VideoCallService } from './video-call.service';

@Component({
  selector: 'mvp-video-call',
  templateUrl: './video-call.component.html',
  styleUrls: ['./video-call.component.css']
})
export class VideoCallComponent implements OnInit {
  callee;
  callerId;
  constructor(private _videoCall: VideoCallService, private _session: SessionService) {
    this._videoCall.calleeId.subscribe(callee => {
      this.callee = callee;
    });
    this._session.select(SESSION_KEY.USER).subscribe((data) => {
      if(data.isLoggedIn) {
        this.callerId = data.user.userID;
      }
    })
  }

  ngOnInit() {}

  call() {
    if(this.callerId) {
      this._videoCall.call(this.callerId, 94617573, 'main_video');
    }
  }
}
