import { SessionService, SESSION_KEY } from './session.service';
import { Injectable } from '@angular/core';
import { SERVICE_NAME } from 'apps/frontend/src/app/modules/core/model/service';
import { HttpClient } from '@angular/common/http';
import { tap, map } from 'rxjs/operators';
import { IAuthPayload, User } from '../model/user';
import { IDoctorListState } from '../model/doctor-list';
import { forkJoin } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(private _http: HttpClient, private _session: SessionService) { }


  logIn(payload: IAuthPayload) {
    return this._http.post(SERVICE_NAME.AUTHENTICATE_USER, payload).pipe(
      tap((response: any) => {
        this._session.setState(SESSION_KEY.USER, {
          isLoggedIn: true,
          user: response as User,
          appointmentDetails: response.appointmentDetails
        });
      }));
  }

  getSpecialityTypes() {
    return this._http.get(SERVICE_NAME.LOOKUP_SPECIALITY_TYPE).pipe(
      tap((response: any) => {
        this._session.setState(SESSION_KEY.DOCTOR_LIST, <IDoctorListState>{
          specialityTypes: response.specialityTypes
        });
      }));
  }


  initLogin(payload: IAuthPayload) {
    return forkJoin([this.logIn(payload), this.getSpecialityTypes()]);
  }

  getDoctorList(specialityType: string) {
    return this._http.get(SERVICE_NAME.DOCTOR_LIST, {
      params: {
        specialityType: specialityType
      }
    }).pipe(
      tap((response: any) => {
        this._session.setState(SESSION_KEY.DOCTOR_LIST, <IDoctorListState>{
          doctorList: {
            [specialityType] : response.doctorList
          }
        });
    }));
  }

  getTimeSlots(specialityType: string, doctorID: string) {
    return this._http.get(SERVICE_NAME.DOCTOR_LIST, {
      params: {
        specialityType: specialityType,
        doctorID: doctorID
      }
    }).pipe(
      map((response:any) => response.appointmentDetails.bookedAppointments)
    );
  }

  bookAppointment(data) {
    return this._http.post(SERVICE_NAME.BOOK_APPOINTMENT, data);
  }

}
