import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { ReactiveFormsModule } from '@angular/forms';

import { HeaderComponent } from './components/header/header.component';
import { FooterComponent } from './components/footer/footer.component';
import { httpInterceptorProviders } from './http-interceptors';
import { LoaderComponent, LoaderService } from './components/loader';
import { ErrTextComponent, ValidationService } from './validators';

@NgModule({
  declarations: [HeaderComponent, FooterComponent, LoaderComponent, ErrTextComponent],
  imports: [
    CommonModule,
    RouterModule,
    ReactiveFormsModule,
    HttpClientModule
  ],
  exports: [
    HeaderComponent,
    FooterComponent,
    LoaderComponent,
    ReactiveFormsModule,
    HttpClientModule,
    ErrTextComponent
  ],
  providers:[ LoaderService, ValidationService, httpInterceptorProviders ]
})
export class CoreModule { }
